import React from "react"


const HourCard = ({hh}) => {

    const time = new Date(hh.time);
    
    return (
        <>
        <div className="hourCard" >
        <p>{time.toLocaleString("en-US", {
                  //weekday: "long",
                  hour: "numeric",
                  minute: "numeric",
                  hour12: true,
                })}</p>
            <div>
                <img alt={hh.condition.text} src={hh.condition.icon}/>
            </div>
            <div>
                <div><span>{hh.temp_c}</span> &#8451;</div>
            </div>
        </div>
      </>
    );
  };

  export default HourCard;

  