import React, { useState, useEffect } from "react";
import "./App.css";
import SearchIcon from "./search.svg";
import HourCard from "./HourCard";
import AutoComplete from "./Autocomplete";
import { editableInputTypes } from "@testing-library/user-event/dist/utils";

const App = () => {
  const API_URL =
    "https://api.weatherapi.com/v1/forecast.json?key=8f0aecf44af440dbb0331318210705&days=1&aqi=yes&alerts=yes&q=";
  const [wd, setWd] = useState([]);
  const [cityName, setCityName] = useState("");
  const [loading, setLoading] = useState(false);
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "numeric",
    minute: "numeric",
    hour12: true,
  };
  var displayDate;

  const SEARCH_API =
    "https://api.weatherapi.com/v1/search.json?key=8f0aecf44af440dbb0331318210705&q=";
  const [searchKey, setSearchKey] = useState("");
  const [places, setPlaces] = useState([]);
  var init = "Hyderabad";
  useEffect(() => {
    getWeather(init);
    setCityName(init);
  }, []);

  const getWeather = async (city) => {
    setLoading(true);
    const data = await fetch(`${API_URL}+${city}`)
      .then((response) => {
        if (!response.ok) throw new Error(response.status);
        else return response.json();
      })
      .then((data) => {
        setLoading(false);
        setWd(data);
      })
      .catch((error) => {
        console.log("error: " + error);
      });
  };

  if (wd.location != null) {
    var currHour = new Date(wd.location.localtime).getHours();
    const today = wd.forecast.forecastday[0].day;
    const astro = wd.forecast.forecastday[0].astro;
    displayDate = new Date(wd.location.localtime).toLocaleString(
      "en-US",
      options
    );
  }

  const returnKey = (event) => {
    if (event.keyCode === 13) {
      alert(cityName);
      getWeather(cityName);
    }
  };

  return (
    <div className="container">
      <div className="app">
        <div className="text-center">
          <h1>Today' Weather</h1>
          <p>You can search a city and its near places by it's name</p>
          <div className="search">
            <div className="search-bar-container">
              <AutoComplete
                cityName={init}
                onSelect={(cityName) => getWeather(cityName)}
              />
            </div>
          </div>
          {loading && <div className="loading"></div>}
          {wd.forecast == null ? (
            ""
          ) : (
            <div>
              <div className="sub-heading">
                {wd.location.name} {", "} {wd.location.region} {", "}
                {wd.location.country}
              </div>
              <div className="heading-section">
                <div className="heading">{displayDate}</div>{" "}
              </div>

              <div className="current">
                <div className="current-item">
                  <div>
                    Feels like {wd.current.feelslike_c} &#8451; {","}
                    <br />
                    {wd.current.condition.text}
                  </div>
                  <div className="temp">
                    <img
                      alt={wd.forecast.forecastday[0].day.condition.text}
                      src={wd.forecast.forecastday[0].day.condition.icon}
                    />
                    <span>{Math.floor(wd.current.temp_c)}</span>
                    <span>°C</span>
                  </div>
                  <div className="temp-maxmin">
                    <span>
                      Max.
                      <br />
                      {wd.forecast.forecastday[0].day.maxtemp_c}&#8451;
                    </span>
                    <span>
                      Min.
                      <br />
                      {wd.forecast.forecastday[0].day.mintemp_c}&#8451;
                    </span>
                    <span>
                      Avg.
                      <br />
                      {wd.forecast.forecastday[0].day.avgtemp_c}&#8451;
                    </span>
                  </div>
                </div>
                <div className="current-item right-side">
                  <div className="sun-moon">
                    <div className="sun-moon-item">
                      <div>
                        <i
                          className="fa-solid fa-cloud-rain fa-lg"
                          title="Preciptation"
                          style={{ color: "HoneyDew" }}
                        ></i>{" "}
                        <span>{wd.current.precip_in}%</span>
                      </div>
                      <div>
                        <i
                          className="fa-solid fa-droplet fa-lg"
                          title="Humidity"
                          style={{ color: "DodgerBlue" }}
                        ></i>{" "}
                        <span>{wd.current.humidity}%</span>
                      </div>
                    </div>

                    <div className="sun-moon-item">
                      <div>
                        <i
                          className="fa-solid fa-umbrella fa-lg"
                          title="Chance of Rain"
                          style={{ color: "LightSteelBlue" }}
                        ></i>{" "}
                        <span>
                          {wd.forecast.forecastday[0].day.daily_chance_of_rain}%
                        </span>
                      </div>
                      <div>
                        <i
                          className="fa-solid fa-snowflake fa-lg"
                          title="Chance of Snow"
                          style={{ color: "snow" }}
                        ></i>{" "}
                        <span>
                          {wd.forecast.forecastday[0].day.daily_chance_of_snow}%
                        </span>
                      </div>
                    </div>
                    <div className="sun-moon-item">
                      <div>
                        <i
                          className="fa-regular fa-wind fa-lg"
                          title="Wind Speed"
                          style={{ color: "BurlyWood" }}
                        ></i>{" "}
                        <span>
                          {wd.current.gust_kph}{" "}
                          <span style={{ fontSize: "70%" }}>km/h</span>
                        </span>
                      </div>
                      <div>
                        <span>
                          <i
                            className="fa-solid fa-location-arrow"
                            title="Wind Direction"
                            style={{
                              transform:
                                "rotate(" +
                                (wd.current.wind_degree - 40) +
                                "deg)",
                            }}
                          ></i>{" "}
                          <span>
                            {wd.current.wind_dir +
                              " " +
                              wd.current.wind_degree +
                              "°"}
                          </span>
                        </span>
                      </div>
                    </div>
                  </div>
                  <div className="sun-moon">
                    <div className="sun-moon-item">
                      <div>
                        <i
                          className="fa-regular fa-sunrise"
                          title="Sunrise"
                        ></i>{" "}
                        {wd.forecast.forecastday[0].astro.sunrise}
                      </div>
                      <div>
                        <i className="fa-regular fa-sunset" title="Sunset"></i>{" "}
                        {wd.forecast.forecastday[0].astro.sunset}
                      </div>
                    </div>
                    <div className="sun-moon-item">
                      <div>
                        <i className="fa-solid fa-moon" title="Moonrise"></i>{" "}
                        {wd.forecast.forecastday[0].astro.moonrise}
                      </div>
                      <div>
                        <i className="fa-regular fa-moon" title="Noonset"></i>{" "}
                        {wd.forecast.forecastday[0].astro.moonset}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="heading">Air Quality Index</div>
              <div className="current aqi">
                <div>
                  PM 2.5 <br />
                  {wd.current.air_quality.pm2_5.toFixed(2)}
                </div>
                <div>
                  PM 10 <br />
                  {wd.current.air_quality.pm10.toFixed(2)}
                </div>
                <div>
                  NO2 <br /> {wd.current.air_quality.no2.toFixed(2)}
                </div>
                <div>
                  SO2 <br />
                  {wd.current.air_quality.so2.toFixed(2)}
                </div>
                <div>
                  CO <br />
                  {wd.current.air_quality.co.toFixed(2)}
                </div>
                <div>
                  OZONE <br />
                  {wd.current.air_quality.o3.toFixed(2)}
                </div>
              </div>
              <div className="heading">Hourly Forecast</div>
              <div className="hourCards">
                {wd.forecast.forecastday[0].hour.map((h, i) =>
                  new Date(h.time).getHours() > currHour ? (
                    <HourCard hh={h} key={i} />
                  ) : (
                    ""
                  )
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
export default App;
