import React, { useState, useRef, useEffect, useMemo } from "react";
import AutoCompleteItem from "./AutoCompleteItem";

const AutoComplete = ({cityName ,onSelect}) => {

    const SEARCH_API =
    "https://api.weatherapi.com/v1/search.json?key=8f0aecf44af440dbb0331318210705&q=";
    const [searchKey, setSearchKey] = useState("");
    const [places, setPlaces] = useState([]);
    const [loading, setLoading] = useState(false);

    const getPlaces = async (place) => {
        setLoading(true);
        const data = await fetch(`${SEARCH_API}+${place}`)
          .then((response) => {
            if (!response.ok) throw new Error(response.status);
            else return response.json();
          })
          .then((data) => {
            setPlaces(data);
            setLoading(false);
          })
          .catch((error) => {
            console.log("error: " + error);
          });
        
    };

    const [isVisbile, setVisiblity] = useState(false);
    const [search, setSearch] = useState("");
    const [cursor, setCursor] = useState(-1);

    const searchContainer = useRef(null);
    const searchResultRef = useRef(null);

    useEffect(() => {
        setSearch(cityName)
    },[]);
    useEffect(() => {
        window.addEventListener("mousedown", handleClickOutside);

        return () => {
            window.removeEventListener("mousedown", handleClickOutside);
        };
    }, []);

    const scrollIntoView = position => {
        searchResultRef.current.parentNode.scrollTo({
            top: position,
            behavior: "smooth"
        });
    };

    useEffect(() => {
        if (cursor < 0 || cursor > suggestions.length || !searchResultRef) {
            return () => {};
        }

        let listItems = Array.from(searchResultRef.current.children);
        listItems[cursor] && scrollIntoView(listItems[cursor].offsetTop);
    }, [cursor]);

    const suggestions = useMemo(() => {
        if (!search) return places;

        setCursor(-1);
        scrollIntoView(0);
        return places;
        // return places.filter(item =>
        //     item.name.toLowerCase().includes(search.toLowerCase())
        // );
    }, [places, search]);

    const handleClickOutside = event => {
        if (
            searchContainer.current &&
            !searchContainer.current.contains(event.target)
        ) {
            hideSuggestion();
        }
    };

    const showSuggestion = () => setVisiblity(true);
    const hideSuggestion = () => setVisiblity(false);

    const keyboardNavigation = e => {
        if (e.key === "ArrowDown") {
            isVisbile
                ? setCursor(c => (c < suggestions.length - 1 ? c + 1 : c))
                : showSuggestion();
        }

        if (e.key === "ArrowUp") {
            setCursor(c => (c > 0 ? c - 1 : 0));
        }

        if (e.key === "Escape") {
            hideSuggestion();
        }

        if (e.key === "Enter" && cursor > 0) {
            setSearch(suggestions[cursor].name);
            hideSuggestion();
        }
    };
    return (
        <div  style={{ height: "100%"}} ref={searchContainer}>
            
            <input
                type="text"
                name="search"
                autoComplete="off"
                value={search}
                onClick={showSuggestion}
                onChange={e => 
                    {
                    getPlaces(e.target.value);
                    setSearch(e.target.value);
                    showSuggestion();
                    }}
                onKeyDown={e => keyboardNavigation(e)}
            />
            <i className="fas fa-spinner fa-lg fa-spin" style={{visibility: (loading ? "visible":"hidden")}}></i>
            <div
                className={`search-result ${
                    isVisbile ? "visible" : "invisible"
                }`}
            >
                <ul className="list-group" ref={searchResultRef}>
                    {suggestions.map((item, idx) => (
                        <AutoCompleteItem
                            key={item.name}
                            onSelectItem={() => {
                                hideSuggestion();
                                setSearch(item.name);
                                onSelect(item.name+','+item.country);
                            }}
                            isHighlighted={cursor === idx ? true : false}
                            {...item}
                        />
                    ))}
                </ul>
            </div>
        </div>
    );
};

export default AutoComplete;
